let mapStateToProps = (state: any) => ({
    persons: getPersonsSelector(state),
    pageSize: getPageSize(state),
    totalUsersCount: getTotalUserCount(state),
    currentPage: getCurrentPage(state),
    isFetching: getIsFetching(state),
    followingInProgress: getFollowingInProgress(state)
});

let mapDispatchToProps = (dispatch: any) => ({
    // follow: (userid: number) => dispatch(followActionCreator(userid)),
    // unfollow: (userid: number) => dispatch(unfollowActionCreator(userid)),
    //setUsers: (users: any) => dispatch(setUsersActionCreator(users)),
    changeCurrentPage: (currentPage: number) => dispatch(changeCurrentPageActionCreator(currentPage)),
    //setTotalUsersCount: (users: number) => dispatch(setTotalUsersCountActionCreator(users)),
    //isFetchingStatus: (status: boolean) => dispatch(isFetchingActionCreator(status)),
    // isFollowingInProgress: (status: boolean, userid: number) => dispatch(followingInProgressActionCreator(status, userid)),
    getUsersThunk: (currentPage: number, pageSize: number) => dispatch(getUsersThunk(currentPage, pageSize)),
    isFollowingThunk: (userid: number) => dispatch(isFollowingThunk(userid)),
    isUnFollowingThunk: (userid: number) => dispatch(isUnFollowingThunk(userid))
});

// const withRedirectComponent = withAuthRedirect(UsersClass);

const UsersContainer = compose(
    connect(mapStateToProps, mapDispatchToProps),
    withAuthRedirect
)(UsersClass);

// const UsersContainer = connect(mapStateToProps, mapDispatchToProps)(withRedirectComponent);

export default UsersContainer;