import React, { useState } from 'react';
import style from './Users.module.css';
import cn from 'classnames';
export const Paginator = (props: any) => {
    let pageCount = Math.ceil(props.totalUsersCount / props.pageSize);
    let pages = [];
    for (let i = 1; i <= pageCount; i++) {
        pages.push(i);
    }

    let partionSize = 10;
    let partionCount = Math.ceil(pageCount / partionSize);
    let [positionNumber, setPositionNumber] = useState(1);
    let leftPageNumber = (positionNumber - 1) * partionSize + 1;
    let rightPageNumber = positionNumber * partionSize;


    return(
        <div className={cn(style.pagination, {[style.click]: false})}>
        {positionNumber > 1 && <button onClick={() => setPositionNumber(positionNumber - 1)}>prev</button>}
        {pages.filter((i:any) => i >= leftPageNumber && i <= rightPageNumber).map((item: any) => <button onClick={() => props.changeCurrentPage(item)} className={props.currentPage === item ? style.selected : ''}>{item}</button>)}
        {partionCount >= positionNumber && <button onClick={() => setPositionNumber(positionNumber + 1)}>next</button>}
    </div>
    );
}