import React from 'react';
import style from './Users.module.css';
import photoUser from './../assets/images/users.png';
import { Link } from 'react-router-dom';
import { Paginator } from './Paginator';
const UserFunc = (props: any) => {
    // let pageCount = Math.ceil(props.totalUsersCount / props.pageSize);
    // let pages = [];
    // for (let i = 1; i <= pageCount; i++) {
    //     pages.push(i);
    // }
    return (
        <div>
            <Paginator {...props} />
            {/* <div className={style.pagination}>
                {pages.map(item => <button onClick={() => props.changeCurrentPage(item)} className={props.currentPage === item ? style.selected : ''}>{item}</button>)}
            </div> */}
            {
                props.persons.map((item: any) => (
                    <div>
                        <span>
                            <div>
                                <Link to={"/articles/" + item.id}><img className={style.image} src={item.photos.small !== null ? item.photos.small : photoUser} alt=''/></Link>
                            </div>
                            <div>
                                {item.followed ? <button disabled={props.followingInProgress.some((id: number) => id === item.id)} onClick={() => {
                                    props.isUnFollowingThunk(item.id);
                                    // axios
                                    // .delete(`https://social-network.samuraijs.com/api/1.0/follow/${item.id}`, {withCredentials: true, headers: {
                                    //     'API-KEY': '15325bdc-bc4f-4fed-b921-7b3eac784eff'
                                    // }})
                                    // props.isFollowingInProgress(true, item.id);
                                    // UsersAPI.unFollowUsersAPI(item.id).then(response => {
                                    //     if (response.resultCode === 0) props.isUnFollowed(item.id);
                                    //     props.isFollowingInProgress(false, item.id);
                                    // })
                                }}>Unfollow</button> : <button disabled={props.followingInProgress.some((id: number) => id === item.id)} onClick={() => {
                                    props.isFollowingThunk(item.id);
                                    // axios
                                    // .post(`https://social-network.samuraijs.com/api/1.0/follow/${item.id}`, {}, {withCredentials: true, headers: {
                                    //     'API-KEY': '15325bdc-bc4f-4fed-b921-7b3eac784eff'
                                    // }})
                                    // props.isFollowingInProgress(true, item.id);
                                    // UsersAPI.followUsersAPI(item.id).then(response => {
                                    //     if (response.resultCode === 0) props.isFollowed(item.id);
                                    //     props.isFollowingInProgress(false, item.id);
                                    //     console.log(props);
                                    // })
                                }}>Follow</button>}
                            </div>
                        </span>
                        <span>
                            <span>
                                <div>{item.name}</div>
                                <div>{item.status}</div>
                            </span>
                            <span>
                                {
                                    item.location
                                        ? <div><div>{item.location.city}</div><div>{item.location.county}</div></div>
                                        : ''
                                }
                            </span>
                        </span>
                    </div>
                ))
            }
        </div>
    );
}
export default UserFunc;